body {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #3c3c3c;
  background-color: #f8ffff;
  min-width: 380px;
  padding: 0;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 330px;
  padding: 0 25px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .container {
    padding: 0 60px;
  }
}

.main-header {
  position: relative;
  background: url("../img/mobile/bg-header.svg") no-repeat;
  background-position: 0 315px;
  background-size: contain;
}

.main-header__intro-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 63px;
  padding-bottom: 170px;
}

.main-header__title {
  font-size: 32px;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  color: #1e959b;
  margin: 0;
}

.main-header__description {
  font-weight: 300;
  margin-top: 20px;
  margin-bottom: 150px;
}

.main-header__new-project {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 232px;
  height: 70px;
  background-color: #f05670;
  border-radius: 20px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.main-header__new-project:hover, .main-header__new-project:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

.main-header__partners {
  position: absolute;
  top: 635px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 340px;
  height: 60px;
  background-color: #1e959b;
  padding: 15px 20px;
  opacity: 0.9;
}

.main-header__partners-list {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 77%;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: all ease 0.5s;
}

.main-header__partners-item:first-of-type {
  width: 260px;
  height: 54px;
  background: url("../img/partners/wooter.svg") no-repeat;
}
.main-header__partners-item:nth-of-type(2) {
  width: 173px;
  height: 53px;
  background: url("../img/partners/smartbar.svg") no-repeat;
}
.main-header__partners-item:nth-of-type(3) {
  width: 160px;
  height: 54px;
  background: url("../img/partners/cabify.svg") no-repeat;
}
.main-header__partners-item:nth-of-type(4) {
  width: 139px;
  height: 54px;
  background: url("../img/partners/kari.svg") no-repeat;
}
.main-header__partners-item:last-of-type {
  width: 197px;
  height: 13px;
  background: url("../img/partners/uralairlines.svg") no-repeat;
}

.main-header__button {
  width: 17px;
  height: 22px;
  background: transparent url("../img/mobile/arrow.svg") no-repeat;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-header__button--previus {
  transform: rotate(180deg);
}

.main-nav {
  padding-top: 44px;
}

.main-nav__logo {
  display: block;
  width: 318px;
  height: 77px;
  background: url("../img/logo.svg") no-repeat;
  background-size: cover;
  transition: 0.5s;
}
.main-nav__logo:hover, .main-nav__logo:focus {
  opacity: 0.8;
  transition: 0.5s;
}

.main-nav__link-block {
  display: none;
}

.about {
  padding-top: 33px;
}

.about__preferences-list {
  display: none;
}

.about__info-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../img/mobile/about.png");
  background-repeat: no-repeat;
  background-size: 330px 314px;
  background-position: 0 200px;
}
@media (min-resolution: 2dppx) {
  .about__info-block {
    background-image: url("../img/mobile/about@2x.png");
    background-repeat: no-repeat;
    background-size: 330px 314px;
  }
}

.about__title {
  font-size: 29px;
  line-height: 38px;
  text-transform: uppercase;
  text-align: center;
  color: #1e959b;
  margin: 0;
}

.about__description {
  font-weight: 300;
  margin-top: 27px;
  margin-bottom: 0;
}

.about__link-interview {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 257px;
  height: 70px;
  background-color: #f05670;
  margin-top: 368px;
  border-radius: 20px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.about__link-interview:hover, .about__link-interview:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

@media (min-width: 660px) {
  .about__preferences-list {
    display: block;
  }
}
.projects {
  padding-top: 90px;
}

.projects__intro {
  font-size: 29px;
  line-height: 38px;
  text-transform: uppercase;
  text-align: center;
  color: #1e959b;
  margin: 0;
}

.projects__list {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  margin: 29px 0 0 0;
  padding: 0;
  list-style: none;
}

.projects__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 25px;
  background: linear-gradient(180deg, #FFFFFF 0%, #F7F9FD 100%);
  padding-bottom: 25px;
  padding-left: 45px;
  padding-right: 45px;
  box-shadow: 0px 3px 4px rgba(30, 149, 155, 0.5);
}

.projects__title {
  font-size: 16px;
  line-height: 20px;
  margin-top: 19px;
  margin-bottom: 5px;
}
.projects__title a {
  color: #1e959b;
  text-decoration: none;
  display: inline-block;
  transition: 0.5s;
}
.projects__title a:hover, .projects__title a:focus {
  transform: scale(1.2);
  opacity: 0.7;
  transition: 0.5s;
}

.projects__description {
  font-weight: 300;
  margin: 0;
}

.projects__all-projects {
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #1e959b;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 233px;
  height: 70px;
  margin: 30px auto 0 auto;
  border: 1px solid #1e959b;
  border-radius: 20px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.projects__all-projects:hover, .projects__all-projects:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

.order {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 90px;
  padding-left: 25px;
  padding-right: 25px;
}
.order::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 110px;
  background-color: #1e959b;
}

.order__list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.order__item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
  width: 248px;
  height: 345px;
  background: linear-gradient(180deg, #FFFFFF 0%, #F7F9FD 100%);
  border-radius: 30px;
}
.order__item::before {
  content: "";
  position: absolute;
  top: -60px;
  width: 170px;
  height: 170px;
  background-color: #1e959b;
  border-radius: 50%;
}
.order__item h4 {
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  color: #1e959b;
  margin: 0;
}
.order__item p {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 1px;
  margin-top: 15px;
  margin-bottom: 32px;
}
.order__item a {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f05670;
  width: 207px;
  height: 62px;
  border-radius: 20px;
  margin-bottom: 22px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.order__item a:hover, .order__item a:focus {
  transform: scale(1.05);
  transition: 0.5s;
}
.order__item:first-of-type::before {
  background-image: url("../img/ecom.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.order__item:nth-of-type(2)::before {
  background-image: url("../img/saas.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.order__item:nth-of-type(3)::before {
  background-image: url("../img/mob-app.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.order__button {
  width: 17px;
  height: 22px;
  background: transparent url("../img/mobile/arrow.svg") no-repeat;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.order__button--previus {
  transform: rotate(180deg);
}
.order__button--next {
  transform: rotate(0);
}

.application {
  background-image: url("../img/application.svg");
  background-repeat: no-repeat;
  background-position: 30px center;
  padding: 70px 25px 0 25px;
}

.application__link {
  font-size: 20px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f05670;
  width: 300px;
  height: 80px;
  border-radius: 20px;
  margin: 405px auto 0 auto;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.application__link:hover, .application__link:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

.main-footer__all-info {
  padding-top: 90px;
  padding-bottom: 30px;
}

.main-footer__link-block {
  display: flex;
  justify-content: space-between;
}
.main-footer__link-block a {
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 1px;
  font-weight: 300;
  text-decoration: none;
  color: #1e959b;
}

.main-footer__contact-block {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 35px;
}

.main-footer__link {
  display: block;
  font-weight: 700;
  text-decoration: none;
  color: #1e959b;
}
.main-footer__link--feedback {
  text-align: center;
  flex-basis: 100%;
  margin-bottom: 35px;
}
.main-footer__link--phone {
  position: relative;
  margin-left: 34px;
}
.main-footer__link--phone::before {
  content: "";
  position: absolute;
  top: -7px;
  left: -34px;
  width: 18px;
  height: 32px;
  background: url("../img/phone.svg") no-repeat;
}
.main-footer__link--telegram {
  width: 46px;
  height: 35px;
  background: url("../img/telegram.svg") no-repeat;
  margin-left: auto;
  margin-right: 31px;
}
.main-footer__link--mail {
  width: 46px;
  height: 35px;
  background: url("../img/mail.svg") no-repeat;
}

.main-footer__other-info {
  font-size: 12px;
  line-height: 14px;
  position: relative;
  width: 252px;
  margin-top: 35px;
  margin-bottom: 0;
  margin-left: auto;
}
.main-footer__other-info::before {
  content: "";
  position: absolute;
  top: 15px;
  left: -88px;
  width: 80px;
  height: 84px;
  background: url("../img/footer-list.svg") no-repeat;
}

.main-footer__copyright {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 1px;
  width: max-content;
  margin: 40px auto 0 auto;
  opacity: 0.5;
}